import Plus4U5 from "uu_plus4u5g01";
import * as ProductCatalogue from "./productcatalogue.js";

export * from "./productcatalogue.js";
export default ProductCatalogue;

import ProductCatalogueLsi from './productcatalogue-lsi.js';
Plus4U5.Environment.Lsi = Plus4U5.Environment.Lsi || {};
Plus4U5.Environment.Lsi.ProductCatalogue = ProductCatalogueLsi;

// merge into Plus4U5
// NOTE "plus4u5" must ensure that the key already exists there, otherwise following usage wouldn't work:
for (var k in ProductCatalogue) Plus4U5.ProductCatalogue[k] = ProductCatalogue[k];



